var simpleNoise = require('./noise.js').simpleNoise;
var THREE = require('three');

export default class Curtain {

	constructor({ segmentsHigh = 20, segmentsWide = 80, width = 800000, height = 120000, x = 105000, y = -4000, z = 0 } = {}) {
		this.segmentsHigh = segmentsHigh;
		this.segmentsWide = segmentsWide;
		this.x = x; // displacement up away from earth
		this.y = y; // displacement from curtain face parallel to earth
		this.z = z; // displacement along curtain parallel to earth
		this.height = height;
		this.width = width;
	}

  warp (magnitude = 1.0, phase = 0.0, period = 1.0, delta = false) {
    var v = this.geometry.vertices;
    var stripNum = 0; // strip run from the base of the curtain upwards
    var pos = 0.0;
    const widthPerSegment = this.width / this.segmentsWide;
    for ( var i = 0; i < v.length; i ++ ) {
      stripNum = Math.floor( i / this.segmentsHigh );
      pos = magnitude * Math.sin( (phase + v[i].z / widthPerSegment) / period );
      v[i].y = delta ? v[i].y + pos : pos;
    }
  }

  shaderMaterial () {
    var vShader =
      `
      uniform float time;
      varying vec2 vUv;
      // varying vec3 worldCoord;

      void main () {
        vUv = uv;
        // worldCoord = position;
        gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
      }
      `

    var fShader =
      `
      ${simpleNoise}

      uniform float time;
      varying vec2 vUv;
      // varying vec3 worldCoord;

      void main() {
        float t = time * 0.01;
        float noise = 1.5 * snoise(vec2(t * 4.0 + vUv.x, vUv.y * 3.0));
        noise += 1.0 * snoise(vec2(100.0 + t * 1.2,vUv.y * 1000.0));
        noise *= 1.0 * snoise(vec2(10.0 + t * 2.0 + vUv.x, vUv.y * 3000.0));
        
        noise = (sin(noise) * 0.5 + 0.5) * 0.5 + 0.5;
        float a = (0.5 + sin(vUv.y*10.0 + time * 0.1)) * 0.75 + 0.5;
        a = a * sin(vUv.x * 3.14159265357); // vertical fade
        gl_FragColor = vec4(vUv.y, 1.0 - vUv.x, 0.4, noise * a);
      }
      `

    var uniforms = {
      time: { type: "f", value: 1.0 },
    };

    var shaderMaterial =
      new THREE.ShaderMaterial({
        uniforms: uniforms,
        fragmentShader: fShader,
        vertexShader: vShader,
        wireframe: false,
        side: THREE.DoubleSide,
        transparent: true,
        blending: THREE.AdditiveBlending,
        depthTest: false
      });
    return shaderMaterial;
  }

	mesh() {
		this.geometry = new THREE.PlaneGeometry( this.height, this.width, this.segmentsHigh, this.segmentsWide );
		this.geometry.rotateX( Math.PI / 2.0 );
    this.geometry.rotateY( 0.04 );
    this.material = this.shaderMaterial();
		this.mesh = new THREE.Mesh( this.geometry, this.material );
		this.mesh.rotateZ(Math.PI / 2);
		this.mesh.translateX(this.x);
    this.mesh.translateY(this.y);
    this.mesh.translateZ(this.z);
    this.warp(30000, 0, 4, false);
		return this.mesh;
	}
  
	animate(tick) {
    this.warp(315, tick, 20.0, true);
    this.warp(160, tick, 40.0, true);
    this.warp(25, tick, 1.5, true);
    this.mesh.material.uniforms.time.value = tick;
		this.mesh.geometry.verticesNeedUpdate = true;
    this.mesh.material.needsUpdate = true;
	}
}



