var THREE = require('three');
require('./OrbitControls.js');
import Curtain from './Curtain.js';
var dat = require('./dat.gui.min.js');

// var gui = new dat.GUI();
var container, stats;
var camera, controls, scene, renderer;
var mesh, meshGround, texture, geometry, geometryGround, material, materialGround;
var worldWidth = 128, worldDepth = 128,
worldHalfWidth = worldWidth / 2, worldHalfDepth = worldDepth / 2;
var clock = new THREE.Clock();
var curtains = [];

const SNOW_COLOUR = 0xbbbbee;

init();
animate();

function init() {
	container = document.getElementById( 'container' );

	// camera
	camera = new THREE.PerspectiveCamera( 60, window.innerWidth / window.innerHeight, 1, 900000 );
	camera.position.x = 5000;
	controls = new THREE.OrbitControls(camera, container);
	controls.target.set(
		camera.position.x - 25.0,
		camera.position.y + 5.0,
		camera.position.z
	);

	// scene
	scene = new THREE.Scene();

	// light
	var light = new THREE.PointLight( 0xffffff, 1.5, 50000 );
	light.position.set( 500, 2500, 0 );
	
	scene.add(light);

	// moon
	var moonGeometry = new THREE.SphereGeometry( 300, 32, 32 );
	var moonMaterial = new THREE.MeshPhongMaterial({
		color: 0xffffff,
		lights: true,
		shading: THREE.FlatShading

	}); 
	var moon = new THREE.Mesh( moonGeometry, moonMaterial );
	moon.position.set( 300, 3900, -3600);
	moon.rotateX ( 1.0 );

	scene.add(moon);

	camera.lookAt(moon.position);

	//aurora curtain
	curtains.push(new Curtain({height: 160000, x: 120000}));
	curtains.push(new Curtain({segmentsWide: 100, y:-150000, x: 100000.0, z: 5000}));
	curtains.push(new Curtain({segmentsX: 66, width: 1000000, x: 200000, y:100000, z: 0}));
	curtains.push(new Curtain({segmentsX: 116, width: 1000000, x: 300000, y:0, z: 4000}));
	curtains.forEach( c => scene.add(c.mesh()));

	// ground
	geometryGround = new THREE.PlaneGeometry( 20000, 20000, 300, 300 );
	geometryGround.rotateX( -Math.PI / 2 );

	var v = geometryGround.vertices;
	for (var i=0; i< v.length; i++){
		v[i].y = Math.sin(v[i].z/200) * 40;
		v[i].y += Math.sin(v[i].x/221) * 32;
		v[i].y -= (Math.sin(v[i].x / 2100 * Math.PI) + 0.5) * 120;
		v[i].y -= (Math.sin(v[i].z / 2000 * Math.PI) + 0.5) * 200;
	}
	materialGround = new THREE.MeshPhongMaterial( {
		color: SNOW_COLOUR,
		wireframe: false,
		lights: true,
		shading: THREE.FlatShading
	  } );
	meshGround = new THREE.Mesh( geometryGround, materialGround );
	meshGround.translateY(-400);
	scene.add( meshGround );

	renderer = new THREE.WebGLRenderer();
	renderer.setClearColor( 0x111133 );
	renderer.setPixelRatio( window.devicePixelRatio );
	renderer.setSize( window.innerWidth, window.innerHeight );
	container.innerHTML = "";
	container.appendChild( renderer.domElement );
	window.addEventListener( 'resize', onWindowResize, false );
}

function onWindowResize() {
	camera.aspect = window.innerWidth / window.innerHeight;
	camera.updateProjectionMatrix();
	renderer.setSize( window.innerWidth, window.innerHeight );
	// controls.handleResize();
}

function animate() {
	requestAnimationFrame( animate );
	render();
}

function render() {
	var delta = clock.getDelta(),
	time = clock.getElapsedTime() * 10;
	curtains.forEach( c => c.animate(time));

	controls.update( delta );
	renderer.render( scene, camera );
}



